.App {
  background-color: #80aebe
}
.topo, .rodape {
  width: 100%;
  height: 80px;
  background-color: #253a57;
}

.container-intro {
  max-width: 800px;
  width: 100%;
  padding: 40px 30px;
  margin: 60px auto;
  background-color: #ffffff;
  text-align: center;
}
.container-intro h2 {
  font-size: 32px;
  margin: 20px 0;
  background-color: #cec2b4;
}
.container-intro h3 {
  background-color: #cec2b4;
  margin-top: 50px
}
.container-intro img {
  background-color: #80aebe;
  width: 400px;
  border-radius: 200px;
}
.container-box {
  margin: 70px 0;
  display: flex;
}

.box1 {
  max-height: 80px;
  background-color: #80aebe;
  margin: 50px 20px 20px 20px;
}
.box1 p {
  background-color: #253a57;
  color: #ffffff;
  font-size: 22px;
  margin-top: 50px;
}
.box2 {
  margin-top: 40px;
  text-align: center;
}
.box2 p {
  margin: 40px 20px;
}
.box-intro {
  width: 100%;
  max-width: 400px;
}

.container-programadores {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 40px 30px;
}
.container-programadores h3 {
  background-color: #cec2b4;
  width: 100%;
  margin: 20px;
  padding: 10px 0;
  text-align: center;
}
.programadores {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.box-programadores {
  width: 200px;
  text-align: center;
  margin: 30px 20px;
}
.box-programadores img {
  width: 150px;
  height: 150px;
  margin: 30px 0;
  border-radius: 5px;
}

.container-api {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #ffffff;
  width: 100%;
  max-width: 800px;
  padding: 40px 30px;
  margin: 60px auto;
}

.container-api p{
  margin-top: 40px;
  text-align: center;
}
.container-api img {
  width: 150px;
}

.box-api img {
  width: 100%;
  max-width: 600px;
  margin: 50px 0;
  border-radius: 1px;
}

.rodape {
  padding: 30px;
  color: #ffffff;
}


@media (max-width: 646px) {
  .container-box {
    flex-wrap: wrap;
    justify-content: center;
  }
  .box1 {
    max-height: 250px;
  }
  .container-programadores {
    margin: 100px auto;
  }
  .container-api h1 {
    margin: 20px;
    text-align: center;
  }
  .container-api p {
    margin: 10px
  }
  .container-intro img {
    width: 100%;
  }
  .box2 {
    margin-top: 180px;
  }
}